import { api } from '@/utils/request'

/**
 * API
 */
export const spiderApi = {
  /**
   * 
   * @returns {Promise}
   */
  getStatus() {
    return api.get('/spider/status')
  },

  /**
   * 
   * @returns {Promise}
   */
  getConfig() {
    return api.get('/spider/config')
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise}
   */
  startBasic(data) {
    return api.post('/spider/basic/start', data)
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise}
   */
  startRole(data) {
    return api.post('/spider/role/start', data)
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise}
   */
  startEquip(data) {
    return api.post('/spider/equip/start', data)
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise}
   */
  startPet(data) {
    return api.post('/spider/pet/start', data)
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise}
   */
  startProxy(data) {
    return api.post('/spider/proxy/start', data)
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise}
   */
  manageProxy(data = {}) {
    return api.post('/spider/proxy/manage', data)
  },

  /**
   * 
   * @returns {Promise}
   */
  stopTask() {
    return api.post('/spider/task/stop')
  },

  /**
   * 
   * @returns {Promise}
   */
  resetTask() {
    return api.post('/spider/task/reset')
  },

  /**
   * 
   * @param {Object} params - 
   * @param {number} params.lines - 100
   * @param {string} params.type -  (current/recent)current
   * @returns {Promise}
   */
  getLogs(params = {}) {
    return api.get('/spider/logs', params)
  },

  /**
   * 
   * @param {Object} params - 
   * @param {string} params.type -  (role/equip/pet/proxy)role
   * @returns {Promise}
   */
  getProgress(params = {}) {
    return api.get('/spider/progress', params)
  },

  /**
   * 
   * @returns {EventSource}
   */
  streamLogs() {
    // URL
    const baseURL = process.env.NODE_ENV === 'development' 
      ? 'http://localhost:5000' 
      : window.location.origin
    return new EventSource(`${baseURL}/api/v1/spider/logs/stream`)
  },

  /**
   * 
   * @returns {Promise}
   */
  getFiles() {
    return api.get('/spider/files')
  },

  /**
   * 
   * @returns {Promise}
   */
  getLogFiles() {
    return api.get('/spider/logs/files')
  },

  /**
   * 
   * @param {string} filename - 
   * @returns {Promise}
   */
  downloadFile(filename) {
    return api.download(`/spider/download/${filename}`, {}, filename)
  },

  /**
   * Playwright
   * @param {Object} data - Playwright
   * @param {boolean} data.headless - 
   * @param {string} data.target_url - URL
   * @returns {Promise}
   */
  startPlaywright(data) {
    return api.post('/spider/playwright/start', data)
  },

  /**
   * Cookie
   * @returns {Promise}
   */
  checkCookie() {
    return api.get('/spider/cookie/check')
  },

  /**
   * Cookie
   * @returns {Promise}
   */
  updateCookies() {
    return api.post('/spider/cookie/update')
  },

  /**
   * 
   * @param {Object} data - 
   * @param {string} data.url - CBGURL
   * @param {string} data.response_text - 
   * @returns {Promise}
   */
  parseResponse(data) {
    return api.post('/spider/parse/response', data)
  }
} 