import { api } from '@/utils/request'

/**
 * API
 */
export const roleApi = {
  /**
   * 
   * @param {Object} params - 
   * @param {number} params.page - 
   * @param {number} params.page_size - 
   * @param {number} params.year - 
   * @param {number} params.month - 
   * @param {number} params.level_min - 
   * @param {number} params.level_max - 
   * @param {number} params.equip_num - 
   * @param {number} params.pet_num - 
   * @param {number} params.pet_num_level - 
   * @param {string} params.sort_by - 
   * @param {string} params.sort_order - 
   * @param {string} params.role_type - 'normal' 'empty' 
   * @returns {Promise}
   */
  getRoleApi(params = {}) {
    return api.get('/role/', params)
  },

  /**
   * 
   * @param {string} equipId - ID
   * @param {Object} params - 
   * @returns {Promise}
   */
  getRoleDetail(equipId, params = {}) {
    return api.get(`/role/${equipId}`, params)
  },

  /**
   * 
   * @param {string} eid - 
   * @param {Object} params - role_type
   * @returns {Promise}
   */
  deleteRole(eid, params = {}) {
    return api.delete(`/role/${eid}`, params)
  },

  /**
   * 
   * @param {string} eid - 
   * @param {Object} params - role_type/target_role_type
   * @returns {Promise}
   */
  switchRoleType(eid, params = {}) {
    return api.post(`/role/${eid}/switch-type`, params)
  },

  /**
   * 
   * @param {Object} params - 
   * @param {string} params.eid - 
   * @param {number} params.year - 
   * @param {number} params.month - 
   * @param {string} params.role_type - normal
   * @param {string} params.strategy - fair_value
   * @param {number} params.similarity_threshold - 0.7
   * @param {number} params.max_anchors - 30
   * @returns {Promise}
   */
  getRoleValuation(params = {}) {
    return api.post('/role/valuation', params)
  },

  /**
   * 
   * @param {Object} params - 
   * @param {Array} params.eid_list - eid
   * @param {number} params.year - 
   * @param {number} params.month - 
   * @param {string} params.role_type - normal
   * @param {string} params.strategy - fair_value
   * @param {number} params.similarity_threshold - 0.7
   * @param {number} params.max_anchors - 30
   * @param {boolean} params.verbose - false
   * @returns {Promise}
   */
  batchRoleValuation(params = {}) {
    return api.post('/role/batch-valuation', params)
  },

  /**
   * 
   * @param {Object} params - 
   * @param {string} params.eid - 
   * @param {number} params.base_price - 
   * @param {number} params.year - 
   * @param {number} params.month - 
   * @param {string} params.role_type - normal
   * @returns {Promise}
   */
  updateRoleBasePrice(params = {}) {
    return api.post(`/role/${params.eid}/update-base-price`, params)
  },

  /**
   * 
   * @param {Object} params - 
   * @param {string} params.eid - 
   * @param {number} params.year - 
   * @param {number} params.month - 
   * @param {string} params.role_type - normal
   * @param {number} params.similarity_threshold - 0.7
   * @param {number} params.max_anchors - 30
   * @returns {Promise}
   */
  findRoleAnchors(params = {}) {
    return api.post('/role/find-anchors', params)
  },

  /**
   * 
   * @returns {Promise}
   */
  healthCheck() {
    return api.get('/role/health')
  },

  /**
   * 
   * @param {Object} params - 
   * @returns {Promise}
   */
  getRoleStats(params = {}) {
    return api.get('/role/stats', params)
  }

} 