// Cookie - 
const CookieManager = {
  install(Vue) {
    // 
    Vue.prototype.$cookieManager = {
      // 
      getCache() {
        return this.$store.state.cookie.cookieValidationCache
      },

      // 
      setCache(cache) {
        this.$store.commit('cookie/updateCookieCache', cache)
      },

      // 
      reset() {
        this.$store.dispatch('cookie/clearCookieCache')
      }
    }
  }
}

export default CookieManager 